.header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  top: 0;
  z-index: 10;
  width: 100%;
  height: 100px;
  box-shadow: 5px 5px 5px 5px #999;
  overflow: hidden;
}
.header:hover .header_bg {
  opacity: 1;
}
.header .header_bg {
  position: absolute;
  width: 100%;
  height: 100px;
  background-color: #fff;
  opacity: 0.4;
  z-index: -1;
}
.header .logo {
  margin-left: 10%;
  height: 100px;
}
.header .logo:hover > a > img {
  width: 100%;
}
.header .logo:hover > a > img:nth-child(1) {
  display: none;
}
.header .logo:hover > a > img:nth-child(2) {
  display: block;
  transition: 0.3s;
  width: 95%;
  transform: translate(10px, 10px);
}
.header .logo > a {
  width: 100px;
  height: 100px;
}
.header .logo > a > img {
  width: 100%;
  transition: 0.3s;
}
.header .logo > a > img:nth-child(2) {
  display: none;
}
.header .lnb {
  width: 50%;
  height: 100px;
  text-align: center;
  line-height: 130px;
  margin-right: 1%;
}
.header .lnb > ul {
  display: flex;
  justify-content: space-evenly;
}
.header .lnb > ul > li {
  margin-right: 20px;
}
.header .lnb > ul > li > a {
  font-size: 25px;
  font-family: "BodoniFLF-Bold";
  color: #222;
}
.header .lnb > ul > li > a:hover {
  color: #999;
}

.indicate {
  position: absolute;
  z-index: 100;
  /*  top: 50%; */
  right: 20px;
  width: 90px;
}
.indicate > li {
  text-align: center;
  margin-bottom: 30px;
  cursor: pointer;
}
.indicate > li:last-child {
  margin-bottom: 0;
}
.indicate > li > a > p {
  width: 90px;
  margin-right: 30px;
  display: none;
  font-weight: bold;
  font-family: "BodoniFLF";
}
.indicate > li > a .indi_btn {
  margin: 0 auto;
  width: 20px;
  height: 20px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #eee;
}
.indicate > li.on > a > p {
  display: block;
}
.indicate > li.on > a .indi_btn {
  display: none;
}

.wrap > div {
  width: 100%;
