  height: 50px;
  background-color: #C4D9F8;
  background-color: #879ec2;
  /* background-color: #ff9eb1; */
  border-radius: 50%;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 20px;
}
.wrap #page4 .page_box .content_info .content_icon > li:nth-child(1) img {
  transform: translate(1px, -2px);
}
.wrap #page4 .page_box .content_info .content_icon > li:nth-child(4) {
  padding: 5px;
  box-sizing: border-box;
}
.wrap #page4 .page_box .content_info .content_icon > li > img {
  width: 100%;
}
.wrap #page4 .page_box .content_info .content_txt {
  margin-top: 10px;
  margin-left: 35px;
}
.wrap #page4 .page_box .content_info .content_txt > li {
  font-size: 20px;
  margin-bottom: 50px;
  font-weight: bold;
  color: #fff;
}
.wrap #page4 .page_box .content_info .content_txt > li > span {
  font-weight: normal;
}
.wrap #page4 .page_box .content_info .content_txt > li > a {
  font-size: 20px;
  color: #fff;
}
.wrap #page4 .page_box .content_info .content_txt > li > a:hover {
  color: #777;
}
.wrap #page4 .page_box .email {
  position: absolute;
  top: 175px;
  right: 80px;
  width: 500px;
  height: 490px;
  overflow: hidden;
  text-align: center;
  padding-top: 20px;
  margin-left: 100px;
  box-sizing: border-box;
  border: 5px solid #C4D9F8;
  border-radius: 20px;
}
.wrap #page4 .page_box .email > h5 {
  display: block;
  height: 50px;
  line-height: 50px;
  margin-bottom: 10px;
  font-size: 20px;
  color: #444;
}
.wrap #page4 .page_box .email > input {
  width: 450px;
  height: 30px;
  margin-bottom: 10px;
  padding-left: 20px;
  box-sizing: border-box;
  font-size: 14px;
  font-family: "NanumSquare";
  border-radius: 5px;
  background-color: #C4D9F8;
  border: none;
}
.wrap #page4 .page_box .email > textarea {
  width: 450px;
  height: 200px;
  box-sizing: border-box;
  padding-top: 20px;
  padding-left: 20px;
  font-size: 14px;
  font-family: "NanumSquare";
  background-color: #C4D9F8;
  border: none;
}
.wrap #page4 .page_box .email > .btn {
  margin-top: 10px;
  height: 50px;
  /* background-color: $color; */
  background-color: #444;
  color: #fff;
  border: none;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
}
.wrap #page4 .page_box .email > .btn:hover {
  background-color: #C4D9F8;
  color: #fff;
}
.wrap #page4 .page_box > h1 {
  position: absolute;
  top: 130px;
  right: 100px;
  font-family: "BodoniFLF-Bold";
  color: #444;
}

.top_btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  background-color: #C4D9F8;
  color: #fff;
  line-height: 50px;
  text-align: center;
  font-weight: bold;
  letter-spacing: 2px;
  transition: 0.3s;
  border: 2px solid #fff;
  font-size: 18px;
}
.top_btn:hover {
  border-radius: 40%;
}

@keyframes blink-effect {
  50% {
    opacity: 0;
  }
